
import axios from 'axios'
/*import Xheader from '@/components/comother/rheader'*/
import Vue from 'vue';
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

Vue.component(Popup.name, Popup);
export default {
data(){

return {
contents:{rights:'评分说明',title:''},
list:"",
list1:[
{text:"口头汇报",moduleId:11},
{text:"书面汇报",moduleId:12}


],
isYellow:false,
showPop:false,

topShow:false,
showTrans:false,
text:'',
moduleId:''
}
},
filters: {
Upper: function (value) {
try {
if(value=== 1)  throw "待审批";
if(value=== 2)  throw "已审批";
if(value=== 3)  throw "已过期";
}
catch(err) {
return value=err;
}
},
Upper1: function (value) {
try {
if(value===1) throw "";
if(value=== 2)  throw "yellowB";
if(value=== 3)  throw "yellowC";
}
catch(err) {
return value=err;
}

}
},
components:{
XHeader,
ViewBox,
Popup,
Flexbox,
FlexboxItem,
},
directives:{
TransferDom
},
methods:{
change(it){
this.text=it.text;
this.moduleId=it.moduleId;
this.topShow = !this.topShow;
this.showTrans = !this.showTrans;

/*   this.slide1();
this.slide();*/

},
showDet(){
this.topShow = !this.topShow;
this.showTrans = !this.showTrans;
},
clickLink(item) {
if(item.tempint==1){

this.$router.push({
path: '/active/detailPack2/:userId/:departmentid',
name: 'detailPack2',
params:{
userId:item.id,
departmentid:item.departmentid
}
})}else{
this.$router.push({
path: '/active/detailPack3/:userId/:departmentid',
name: 'detailPack3',
params:{
userId:item.id,
departmentid:item.departmentid}
})}

},

getlist1(){
axios({
method: 'get',
url: 'ppartymember/getReportByDepartmentid',
params: {
departmentid:this.$store.getters.user.departmentid,
moudleId:12

}
}) .then((res)=> {
this.list=res.data;
console.log("11111111111111",res.data);
})
.catch(function (error) {
console.log(error);
});
},

getlist2(){
axios({
method: 'get',
url: 'ppartymember/getReportByDepartmentid',
params: {
departmentid:this.$store.getters.user.departmentid,
moudleId:11

}
}) .then((res)=> {
this.list=res.data;
console.log("222222222222",res.data);
})
.catch(function (error) {
console.log(error);
});
},
know(){
this.showPop = false
},
datePick(s){
Date.prototype.toLocaleString = function(){
return this.getFullYear() +'.'+ (this.getMonth()+1)+'.'+this.getDate()
};
return new Date(s).toLocaleString();
},
showMenu(){
this.showPop = true;
}
},
mounted(){
this.getlist1();
this.getlist2();
/* this.getModule();
this.getmoduleid()*/
}
}



